.login-logo{
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
    /* border-radius: 5px;
    border: 1px solid black;
    background-color: black; */
}

.Login{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    background-color: white;
}

.login-container{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 3px solid rgb(107, 177, 204);
    border-radius: 30px;
    padding: 20px;
    background-color: skyblue;
    /* mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)); */
}

.login-container > h2{
    font-weight: 500;
    margin-bottom: 20px;
    color: whitesmoke;
}

.login-container >  form >h5{
    margin-bottom: 5px;
    color: whitesmoke;
}

.login-container > form >input{
    height: 30px;
    margin-bottom: 5px;
    background-color: white;
    width: 98%;
    border-radius: 20px;
}

.login-container > p{
    margin-top: 15px;
    font-size: 12px;
    color: white;
}

.loginSigninButton{
    background-color: white;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-radius: 20px;
    border-color: grey;
}

.loginRegisterButton{
    background-color: white;
    border-radius: 20px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}