.product{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    margin-left: 20px;
    padding: 20px;
    width: 100%;
    max-height: 200px;
    min-width: 100px;
    min-height: 100px;
    z-index: 1;
    background-color: white;
    margin-bottom: 20px;
    border-radius: 30px;
    border: 3px solid lightgrey;
    /* border-color: teal; */
}

.product > img{
    width: 100%;
    max-height: 200px;
    object-fit: contain;
    /* margin-bottom: 15px; */
}

.product-info{
    align-items: center;
    height: 100px;
    margin-bottom: 15px;
    margin-right: 20px;
}

.product-price{
    align-items: center;
    margin-top: 5px;

}

.product-title{
    max-width: 150px;
}

.product-info > button{
    background-color: white;
    margin-top: 10px;
    margin-bottom: 50px;
    padding: 3px 30px;
    border-radius: 20px;
    border: 3px solid lightgrey;
}