.CheckoutProduct{
    display: flex;
    height: 200px;
    background-color: white;
}

.checkoutproduct-Image{
    object-fit: contain;
    width: 200px;
    height: 200px;
}

.checkoutproduct-Info{
    margin-top: 30px;
    padding-left: 50px;
    justify-content: space-between;
    padding: 20px;
}

.checkoutproduct-Info > button{
    margin-top: 20px;
    background-color: white;
    border-radius: 20px;
    width: 200px;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
}