/* header */
.header {
  display: flex;
  align-items: center;
  background-color: white;
  position: sticky;
  top: 0px;
  max-height: fit-content;
  z-index: 10;
}
.header-logo {
  width: 100px;
  object-fit: contain;
  margin-top: -10px;
  margin-bottom: 0px;
  z-index: -2;
  /* margin: 0 20px; */
  /* margin-top: 13px; */
}

.header-search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}
.header-search-input{
    width: 1120px;
    height: 10px;
    padding: 10px;
    margin-right: 20px;
}
.header-search-icon{
    padding: 8px;
    height: 22px;
    background-color: black;
}

.header-nav{
    display: flex;
    justify-content: space-evenly;
}

.header-options{
    margin-top: 5px;
    display: flex;
    flex-direction: row;
    margin-left: 10px;
    margin-right: 10px;
    color: black;
}

.header-optionslineone{
    font-size: 10px;
}
.header-optionslinetwo{
    font-size: 12px;
    font-weight: bold;
}

.header-optionbasket{
    display: flex;
    align-items: center;
    color: black;
}

.header-basketcount{
    margin-left: 10px;
    margin-right: 10px;
}
