.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 150px;
    padding: 20px;
    background-color: white;
    border-radius: 3px;
    margin-left: 20px;
}

.subtotal-gift{
    display: flex;
    align-items: center;
    font-size: 16px;
}

.subtotal-gift > input{
    margin-right: 5px;
}

.subtotal > button{
    background-color: white;
    border-radius: 20px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
}